.bgvideo{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    filter:blur(4px);
    --webkit-filter:blur(4px);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    object-fit: cover;
    z-index: -1;
}

