.top{
    padding: 15vh 0 0 0;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    overflow: hidden;
}

.mid{
    
    font-size: 2rem;
    color:black;
    padding: 2vh 10vw;
    text-align: center;
}

@media screen and (max-width: 960px) {
    .top{
        font-size: 24px;
        text-align: center;
        padding: 15vh 0 0 0;
    }

    .mid{
        font-size: 1rem;
        color:black;
        padding: 2vh 10vw;
        text-align: center;
    }
}

.bgimageAbout{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    object-fit: cover;
    z-index: -1 ;
}

.container{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
}