.foot {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 0;
}

footer {
    font-size: 20px;
    color: white;
    font-weight: 500;
}

@media screen and (max-width: 600px) {

  footer {
    font-size: 15px;
    color: white;
    font-weight: 500;
}

}
