:root {
    --primary: rgb(255, 123, 0);
}

.btn {
    padding: 8px 20px;
    border-radius: 20px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: rgb(255, 123, 0);
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover {
    padding: 8px 20px;
    border-radius: 5px;
    outline: 2px;
    border: 1px solid rgb(255, 123, 0);
    cursor: pointer;
    transform: scale(1.2);
    background: blanchedalmond;
    color: black;
    transition: all 0.3s ease-out;
}