.bgimage {
  position: absolute;
  top: -18vh;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 160vh;
  width: 100vw;
  z-index: -1;
}

.container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.Header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 35vh;
  padding-left: 8vw;
  font-size: 55px;
  cursor: pointer;
}

.mail {
  position: relative;
  display: flex;
  justify-content: center;
  padding-left: 8vw;
  font-size: 55px;
  cursor: pointer;
}

.mail a {
  text-decoration: none;
  color: white;
}

.mail a:hover {
  text-decoration: none;
  transform: scale(1.1);
  border-bottom: 4px solid rgb(255, 123, 0);
  transition: all 0.2s ease-out;
}

@media screen and (max-width: 913px) {
  .Header {
    font-size: 40px;
    cursor: pointer;
    padding-left: 0px;
  }

  .mail {
    font-size: 40px;
    padding-left: 0px;
  }
}

@media screen and (max-width: 600px) {
  .Header {
    font-size: 25px;
    cursor: pointer;
    padding-left: 0px;
  }

  .mail {
    padding-left: 0px;
    font-size: 25px;
  }
}

@media screen and (max-width: 300px) {
  .Header {
    font-size: 20px;
    cursor: pointer;
    padding-left: 0px;
  }

  .mail {
    padding-left: 0px;
    font-size: 20px;
  }
}
