.main{
    font-size: 84px;
    text-align: center;
    padding: 35vh 0 0 0;
    color:whitesmoke;
}

.body{
    text-align: center;
    font-size: 24px;
    color:whitesmoke;
}

.body:hover{
    cursor: pointer;
    font-weight: 200;
    transform: scale(1.1);
    transition: 0.1s ease-in-out;
}

.Socials {
    display: flex;
    justify-content: center;
    padding: 23vh 0px;
    color:rgb(255, 255, 255);
}

.facebook {
    padding: 0px 5vw;
}

.instagram {
    padding: 0px 5vw;
}

.youtube {
    padding: 0px 5vw;
}

.github {
    padding: 0px 5vw;
}

.fab.fa-instagram:hover {
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  -webkit-background-clip: text;
          /* Also define standard property for compatibility */
          background-clip: text;
  -webkit-text-fill-color: transparent;
  transform: scale(1.5);
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.fab.fa-facebook-f:hover {
  color: #3b5998;
  transform: scale(1.5);
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.fab.fa-youtube:hover {
    color: #FF0000;
    transform: scale(1.5);
    transition: 0.3s ease-in-out;
    cursor: pointer;
  }

.fab.fa-github:hover {
    color: #ffffff ;
    transform: scale(1.5);
    transition: 0.3s ease-in-out;
    cursor: pointer;
}


.bgimageMain{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    object-fit: cover;
    z-index: -1 ;
}

.container{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
}

@media screen and (max-width: 960px) {
    .main{
        font-size: 54px;
        text-align: center;
        padding: 35vh 0 0 0;
    }

    .fab {
        color:rgb(255, 255, 255);
        padding: 0 2vw;
    }

    .Socials {
        padding: 195px;
        text-align: center;
    }
    
}

@media screen and (max-width: 655px) {
    .fab {
        color:rgb(255, 255, 255);
        padding: 0 3vw;
    }

    .Socials {
        padding: 20vw;
        text-align: center;
    }
    
}

@media screen and (max-width: 300px) {
    .fab {
        color:rgb(255, 255, 255);
        padding: 0 2vw;
    }

    .Socials {
        padding: 10vw;
        text-align: center;
    }
    
}